@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rubik+Dirt&display=swap');

.portada {
    width: 100%;
    height: 100vh;
    display: flex;
}
.pig{
    width: 10%;
    margin-top: -168px;
}

.parafo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
    font-size: 3rem;
    font-family: 'Rubik Dirt', cursive;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    transition: all 2s ease-in-out;
    animation: movimiento 3s infinite alternate;
}
@keyframes movimiento {
    0% {
        transform: translate(-50%, -50%) scale(1); 
    }
    100% {
        transform: translate(-50%, -50%) scale(1.1); 
    }
}
/*Boton de whatsapp*/
.btnn {
    position: fixed;
    bottom: 50px;
    right: 50px;
    width: 80px;
    height: 75px;
    border: none;
    background: #33c915;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    animation: movi 1s ease-in;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.btnn:hover{
    background: #2ea815;
}
.btnn #what{
    font-size: 50px;
    color: aliceblue;
    
}

@keyframes movi {
    0% {
        transform: scale(0.5);
    }
    50% {
        transform: scale(1.1);
    }
}




@media only screen and (max-width: 600px) {
    .portada {
        max-width: 100%; 
        height: 50vh;
    }
    .pig{
        width: 17%;
        margin-bottom: 110%;
    }
    
}

@media (max-width: 600px) {
    .parafo {
        display: none;
       
    }
    .btnn {
        animation: none;
        width: 60px;
        height: 60px;
        z-index: 1;
        bottom: 20px;
        right: 20px;

    }
    .btnn #what{
        font-size: 35px;
    
    }
    
}
@media (max-width: 915px) {
    .parafo {
        bottom: 15px;
    }
    .btnn {
        animation: none;
        width: 60px;
        height: 60px;
        z-index: 1;
        bottom: 20px;
        right: 20px;

    }
    .pig{
        width: 17%;
        position: absolute;
        bottom: -3.8%;
    }
    
    .btnn #what{
        font-size: 35px;
    
    }
}

@media (max-width: 1800px) {
    .parafo {
        font-size: 2rem;

    }
    .btnn {
        animation: none;
        width: 60px;
        height: 60px;
        z-index: 1;
        bottom: 20px;
        right: 20px;

    }
    .btnn #what{
        font-size: 35px;
    
    }
}