#container-loarder{
    background: rgba(250, 240, 245, 0.9);
    height: 98%;
    width: 100%;
    transition: all 2s ease;
    position: fixed;
    z-index: 10000;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0  ;   
}
.spinner-grow{
   position: absolute;
   bottom: 50%;
   left: 45%;
   right: 50%;
   color: red;
   margin: auto;   

}
