.navbar-brand img{
    width: 60%;
}

.navbar{
    background-color: rgb(34, 37, 37);
    height: auto;

}

/*navegadores del navb*/

.nav-link{
    color: white;
    font-size: 1.3rem;
    font-weight: 600;
    
}
.nav-link:hover, .nav-link:focus{
    color: rgb(245, 107, 44);
    border-bottom: 2px solid white;
    transition: 0.5s ease-in-out;
}

.nav{
    color: rgb(245, 107, 44);
   
}

.nav:hover{
    color: rgb(230, 228, 224);
}


/*menu del toggle*/
.offcanvas {
    background-color: black;
}
.carousel-a, .navbar-toggler-icon{
   font-size: 30px;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 28255, 28255, 1%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}
.navbar-toggler:focus, .btn-close:focus{
    box-shadow: none;
    outline: none;
}

.carousel-a, .btn-close{
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M.293.293a1 1 0 0 1 1.414 0L8 6.586 14.293.293a1 1 0 1 1 1.414 1.414L9.414 8l6.293 6.293a1 1 0 0 1-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 0 1-1.414-1.414L6.586 8 .293 1.707a1 1 0 0 1 0-1.414z'/%3e%3c/svg%3e")

}

/*imagen del toggle*/
.carousel-a, .offcanvas-title, img{
    width: 50%;
}

/*icon*/    
 .icon{
    display: flex;
    gap: 20px;
    margin-right: 20px;
 }   
.bi{
    font-size: 40px;
    
}  
.bi-instagram,.what-0,.bi-facebook{
    color: rgb(233, 227, 233);
    
}
.bi-instagram:hover,.bi-facebook:hover{
    color: rgb(245, 107, 44);
    
    
}





/*responsiva*/

@media (max-width: 991px){
    .icon{
        display: none;
    }
    .navbar-brand img{
        width: 50%;
    }
    .navbar{
        height: auto;
    }
    
}


@media (max-width: 1800px){
    .navbar-brand img{
        width: 70px;
    }
    
    .nav-link{
        font-size: 0.8rem;
    }
    .bi{
        font-size: 30px;
    }
    .navbar-toggler-icon{
        font-size: 33px;
    }
}
