
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Fira+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Lugrasimo&family=Platypi:ital,wght@0,300..800;1,300..800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rubik+Dirt&display=swap');



.marcas-container{
    height: 80vh;
    justify-content: center;
    margin-top: 60px;
    display: flex;
}


.container-section h1{
    text-align: center;
    margin-top: 120px;
    font-family: 'Rubik Dirt', cursive;
   
    
}

/*fotos de nuestro producto*/
.container-img img{
    height: 300px;
    width: 100%;
    display: block;
}
.container-img{
    border: 2px solid black;
    animation: animacion 0.3s ease-in ;
    margin-bottom: 30px;
    
}
@keyframes animacion {
    0%{
        transform: scale(0.9);
    }
    100%{
        transform: scale(1.0);
    }
    
}
.Project-container{
    padding-top: 60px;
    margin-bottom: 40px;
    padding-bottom: 60px;
}
.container-img p{
    font-size: 25px;
    font-family: "Fira Sans", sans-serif;
    font-weight: 200;
    font-style: normal;
}
/*contenedor de descripcion del producto*/
.container-f2{
    background-color: rgb(34, 37, 37);
    height: 391px;
    width: 100%;
    color: rgb(255, 247, 247);
    padding: 0;
    display: flex;
    align-items: center;
    text-align: center;

}
 
.container-f2 p{
    text-align: center;
    font-size: 24px;
    font-weight: 700;
    width: 50%;
    padding-left: 100px;
    font-family: "Fira Sans", sans-serif;
    font-weight: 200;
    font-style: normal;
    
    
}
.img-container img{
    border-radius: 50%;
    border-top-left-radius: 0%;
    border-bottom-left-radius: 0%;
    height: 31vh;
    width: 45vh;
    
}
/*Boton del link*/
.LinkBtn{
    background-color: rgb(223, 91, 15);
    border: none;

}


/*Responsiva*/


@media only screen and (max-width: 767px) {
    .container-f2{
        height: 391px;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        
        
    }
    .fila{
        margin: auto;
    }
    .img-container img{
        height: 26vh;
        width: 100%;
        border-radius: 50%;
        
        
    }
    .container-f2 p{
        font-size: 15px;
        padding: 10px;
        width: 100%;
   
    }



    .container-img{
        margin-top: 20px;
        
    }
    .col {
        width: 100%;
        margin: auto;
    }
    .col-12 img{
        height: 60%;
    }
    .container-section h1{
        margin-top: 20px;
    }
    .container-img p{
        font-size: 15px;
        width: 100%;
        
    }
   
        
}

   
@media (max-width: 1800px) {
    
    .container-img{
        margin-top: 20px;
        
    }
    .col-12 {
        width: 70%;
        margin: auto;
    }
    .col-12 img{
        height: 60%;
    }
    .container-section h1{
        margin-top: 20px;
    }
    .container-img p{
        font-size: 15px;
        
    }
    .container-f2 p{
        font-size: 15px;
        padding: 10px;
   
    }
} 